<template>
  <div class="justify-center w-full">
    <div class="flex justify-center">EDIT PIC</div>
    <div class="flex justify-center">
      <div class="text-red-600">{{ errorMessage }}</div>
      <div class="w-96 h-96">
        <iframe
          ref="myiframe"
          src="/kbEditPic/main.html"
          width="100%"
          height="100%"
          scrolling="no"
          frameborder="0"
        >
        </iframe>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loadingPic: false,
      loadingSuccess: false,
      errorMessage: "",
    };
  },
  async mounted() {
    const tilesToText = function (tiles) {
      const arr = [];
      for (let i = 0; i < tiles.length; i++) {
        arr.push(tiles[i].toString(16));
      }
      return arr.join("");
    };

    let store = this.$store;
    let iframeWin = this.$refs.myiframe.contentWindow;
    let loginToken = localStorage.getItem("loginToken");
    let userUid = store.getters.userUid;

    const picUid = this.$route.params.pic_uid;
    console.log("EditPic", picUid, store.getters.userUid);

    window.addEventListener("message", async (event) => {
      if (event.data.cmd === undefined) return;
      const recv = event.data;
      // console.log("EditPic recv message", recv);

      if (recv.cmd == "kbEditPic.loadingComplete") {
        await store.wait(1000);

        const reqWeb = {
          api: "getPicture",
          loginToken,
          userUid,
          picUid,
        };

        const res = await store.callAsync(reqWeb);
        if (res.code != 100) {
          this.errorMessage = res.message;
          return;
        }

        console.log("getPicture res", res);

        const arr = [];
        for (let i = 0; i < res.picture.pic_data.length; i++) {
          const c = res.picture.pic_data[i];
          arr.push(parseInt(c, 16));
        }

        iframeWin.postMessage(
          {
            cmd: "kbEditPic.firstData",
            tiles: arr,
          },
          "*"
        );
      } else if (recv.cmd == "kbEditPic.save") {
        const reqWeb = {
          api: "savePicture",
          loginToken,
          userUid,
          picUid,
          picTileText: tilesToText(recv.tiles),
        };

        const res = await store.callAsync(reqWeb);
        if (res.code != 100) {
          this.errorMessage = res.message;
          return;
        }

        iframeWin.postMessage(
          {
            cmd: "kbEditPic.saveComplete",
          },
          "*"
        );
      } else {
        console.error("EditPci unknown cmd", recv.cmd);
      }
    });
  },
};
</script>
